import React from 'react';
import { Badge } from 'reactstrap';

const Example = () => {
  return (
    <div>
      <Badge color="primary">4</Badge>{" "}
      <Badge color="primary">
        <i className="fas fa-star md-12 align-middle"></i> 99
      </Badge>{" "}
      <Badge color="success">
        <i className="fas fa-check md-12 align-middle"></i> 14
      </Badge>{" "}
    </div>
  );
};

export default Example;



